import { copyText, getSearch } from '@/utils'
import iconOfficial from '@/assets/successRegistration/WeiMiao/icon_official.png'
import page, { getPage } from './data.js'
export default {
  async beforeRouteEnter (to, from, next) {
    const pageId = getPage()
    console.log(pageId)
    if (pageId === 'D') {
      next({ name: 'MiniApp', query: { ...to.query } })
      return
    }
    next()
  },
  data () {
    const pageId = getPage()
    return {
      page: page[pageId],
      image: iconOfficial
    }
  },
  created () {
    const { protocol, host, link, ...rest } = getSearch()
    this.queryData = rest
    this.$log.show({
      module_part: '106_tf_h5_00079',
      module_ori: '基于页面',
      alternate: JSON.stringify({
        pagename: '微淼-公众号引导页',
        ab_key: 'Throw_guide_split_flow',
        ab_group: localStorage.getItem('pageId'),
        ab_uniq_user: localStorage.getItem('uniqFlag'),
        ...this.queryData
      })
    })
  },
  methods: {
    copy (text) {
      this.$log.click({
        module_part: '106_tf_h5_00080',
        module_ori: '一键复制按钮',
        alternate: JSON.stringify({
          pagename: '微淼-公众号引导页',
          buttonname: '点击一键复制按钮',
          ab_key: 'Throw_guide_split_flow',
          ab_group: localStorage.getItem('pageId'),
          ab_uniq_user: localStorage.getItem('uniqFlag'),
          ...this.queryData
        })
      })
      copyText(text)
      window.location.href = 'weixin://'
    }
  }
}
